/* раздел с фотографией машины и слайдером для выбора одной из трех фотографий STYLES ARE BELOW  */
.picture-slider__picture .fullpic__inactive {
  display: none; }

.picture-slider__slider {
  margin-top: 35px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.picture-slider__slider .slider__button {
  width: 52px;
  height: 52px;
  border-radius: 50%;
  border: 1px solid #D7D9DF;
  background-color: #ffffff; }

.slider__button-arrow-left {
  border-bottom: 1px solid #48494D;
  border-left: 1px solid #48494D;
  transform: rotate(45deg);
  display: block;
  position: relative;
  width: 10px;
  height: 10px;
  top: 0;
  left: 16px; }

.slider__button-arrow-left::after {
  content: "";
  width: 20px;
  height: 1px;
  position: absolute;
  background: #48494D;
  transform: rotate(-45deg) translateY(-4px) translateX(-7px); }

.slider__button-arrow-right {
  border-bottom: 1px solid #48494D;
  border-left: 1px solid #48494D;
  transform: rotate(225deg);
  display: block;
  position: relative;
  width: 10px;
  height: 10px;
  top: 0;
  left: 22px; }

.slider__button-arrow-right::after {
  content: "";
  width: 20px;
  height: 1px;
  position: absolute;
  background: #48494D;
  transform: rotate(-45deg) translateY(-5px) translateX(-7px); }

.slider__button-inactive {
  border-color: #D7D9DF;
  pointer-events: none; }

.slider__button-inactive::after {
  background-color: #D7D9DF; }

